
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import StatList from '@/components/attendance/StatList'
import StateList from '@/components/attendance/StateList'
import RiliTable from '@/components/attendance/RiliTable'
import SpecialStatInfo from '@/components/attendance/SpecialStatInfo'
import LeaveList from '@/components/attendance/LeaveList'
import { format } from '@/utils/format'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'TeacherAttendanceHome',
components: {
StatList,
XButton,
Flexbox,
FlexboxItem,
Group,
Cell,
StateList,
RiliTable,
SpecialStatInfo,
LeaveList,
LingLoading,
XHeader
},
data() {
return  {
statList: [
{ label: "", number: 99 },
{ label: "", number: 99 },
{ label: "", number: 99 }
],
stateList: [
{state: "", color: "blue"},
{state: "", color: "red"},
{state: "", color: 'gray'}
],
leaveList: [,,,,,],
time: new Date('2016-07-28'),
specificDates: [{time: new Date(), type: 2}, {time: new Date(), type: 1}, {time: new Date('2017-8-29'), type: 2}],
specificColors: {1: 'blue', 2: 'gray'},
specialInfo: [
{state: "", color: "red", number: 3, unit: ''},
{state: "", color: "blue", number: 2, unit: ''},
{state: "", color: "green", number: 1, unit: ''}
]
}
},
methods: {
handleDetail: function(leave) {
this.$router.push('/teacher/attendance/leaveDetail')
},
handleLeave: function() {
this.$router.push('/teacher/attendance/teacherLeave')
},
fetchData: function (formData = {}) {

let user = sessionStorage.getItem('CURRENT_USER')
this.$store.dispatch('actionTeacherAttendance', {...{date: this.getSelectTime, staffId: JSON.parse(user).staffId}, ...formData})
}
},
computed: {
/**
* 
* @return {Array} 
*/
getStatList: function() {
let info = this.$store.getters.getTeacherAttendance
return [
{ label: "", number: info.allNum },
{ label: "", number: info.arriveNum },
{ label: "", number: info.noArriveNum }
]
},
/**
* 
* @return {Array} 
*/
getSpecificDates: function() {
let arr = [], list = this.$store.getters.getTeacherAttendance.teacherAttendance
for (let l in list) {
switch(list[l].sign_status) {
case 2:
case 4:
break
case 3:
case 6:
arr.push({
time: list[l].sign_date,
type: 1
})
break
case 5:
arr.push({
time: list[l].sign_date,
type: 2
})
break
case 1:
arr.push({
time: list[l].sign_date,
type: 3
})
break
case 7:
arr.push({
time: list[l].sign_date,
type: 1
})
arr.push({
time: list[l].sign_date,
type: 2
})
break
default:
console.log('not know type')
}
}

return arr
},
/**
* 
* @returns  {Array} 
*/
getStateList: function () {
return this.$store.getters.getTeacherAttendanceType
},
/**
* 
* @returns {Object}  
*/
getSpecificColors: function () {
return this.$store.getters.getTeacherAttendanceColor
},
/**
* 
* @returns {String}  YYYY-MM
*/
getViewTime: function () {
return format(new Date(this.getSelectTime), 'YYYYMM')
},
/**
* 
* @returns {Object}
*/
getLoadingState: function () {
return this.$store.getters.getLingLoading
},
/**
* 
* @returns {String}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
},
getTime: function() {
return new Date(this.getSelectTime)
}
},
watch: {
/**
* 
* @param val
*/
getLoadingState: function(val) {
if(val.state === 'error') {
this.$vux.toast.show({
text: val.message,
type: 'text'
})
}
},
/**
* 
* @param val
*/
getSelectTime: function(val) {
this.fetchData({date: val})
}
},
created () {
this.fetchData()
}
}



